.content.remote__control>.page__title__wrap {
  grid-column: span 1/ span 1;
}

.section__grid__wrap.remote__control {
  grid-template-columns: repeat(1, minmax(0, 1fr));
}

.content.remote__control section .top .btn__wrap button:first-of-type {
  margin-right: 12px;
}

.content.remote__control .system__control .section__inner>.top {
  margin-bottom: 0px;
}

.content.remote__control .system__control .section__inner>.btn__wrap>ul {
  display: flex;
  flex-wrap: wrap;
}

.content.remote__control .system__control .section__inner>.btn__wrap>ul li {
  margin-right: 16px;
  margin-top: 16px;
}

.content.remote__control section.station__control>.section__inner>.common__grid {
  overflow-x: auto;
}

.content.remote__control .station__control .section__inner>.top {
  margin-bottom: 0px;
}

@media (max-width: 500px) {
  .content.remote__control .system__control .section__inner>.top {
    flex-direction: column;
    align-items: flex-start;
  }

  .content.remote__control .system__control .section__inner>.top>p {
    margin-bottom: 16px;
  }
}